export function toPackages(data, sessionId) {
  const bodySize = 18;
  const packagesCount = Math.ceil(data.byteLength / bodySize);
  const packagesCountBuffer = new Uint8Array(2).buffer;
  const res: Uint8Array[] = [];

  new DataView(packagesCountBuffer).setUint16(0, packagesCount);

  for (let i = 0; i < packagesCount; ++i) {
    const packageNumberBuffer = new Uint8Array(2).buffer;

    new DataView(packageNumberBuffer).setUint16(0, i);

    const header = appendBuffer(
      new Uint8Array([sessionId]).buffer,
      appendBuffer(packageNumberBuffer, packagesCountBuffer)
    );

    const buffer = appendBuffer(
      header,
      data.slice(i * bodySize, (i + 1) * bodySize).buffer
    );

    res.push(new Uint8Array(buffer));
  }

  return res;
}

export function appendBuffer(buffer1, buffer2): ArrayBuffer {
  const tmp = new Uint8Array(buffer1.byteLength + buffer2.byteLength);

  tmp.set(new Uint8Array(buffer1), 0);
  tmp.set(new Uint8Array(buffer2), buffer1.byteLength);

  return tmp.buffer;
}

export async function sendPackages(characteristic, packages) {
  let i = 0;

  while (i < packages.length) {
    await characteristic.writeValue(packages[i++]);
  }
}
