import { CHARACTERISTIC, SBERCAST_BLE_SERVICE_UUID } from "./constants";
import { Root, load } from "protobufjs";
import { sendPackages, toPackages } from "./utils";
import { WifiConnectionType } from "./types";

export class SbercastBLEService {
  static instance: SbercastBLEService | null = null;

  private writableCharacteristic: BluetoothRemoteGATTCharacteristic | undefined;
  private readableCharacteristic: BluetoothRemoteGATTCharacteristic | undefined;
  private sbercastBleService: BluetoothRemoteGATTService | undefined;
  private sessionId: number = 0;
  private protoRoot: Root | undefined;
  private irdRequestProtoType;
  private irdResponseProtoType;
  private starBleMessageProtoType;

  static getInstance(): SbercastBLEService {
    if (!this.instance) {
      this.instance = new SbercastBLEService();
    }

    return this.instance;
  }

  async setWritable() {
    this.writableCharacteristic =
      await this.sbercastBleService?.getCharacteristic(CHARACTERISTIC.WRITABLE);
  }

  async setReadable() {
    this.readableCharacteristic =
      await this.sbercastBleService?.getCharacteristic(CHARACTERISTIC.READABLE);
  }

  async setSession() {
    const sessionCharacteristic =
      await this.sbercastBleService?.getCharacteristic(CHARACTERISTIC.SESSION);
    const sessionDataView = await sessionCharacteristic?.readValue();

    if (!sessionDataView) {
      throw new Error("Crete session failed!");
    }

    this.sessionId = new Uint8Array(sessionDataView?.buffer)[0];
  }

  async initProto() {
    this.protoRoot = await load("./proto/main.proto");
    this.starBleMessageProtoType = this.protoRoot.lookupType(
      "main.StarBLEMessage"
    );
    this.irdRequestProtoType = this.protoRoot.lookupType("main.IrdRequest");
    this.irdResponseProtoType = this.protoRoot.lookupType("main.IrdResponse");
  }

  async init() {
    if (!("bluetooth" in navigator)) {
      throw new Error("Web BLE not supported!");
    }

    // TODO: parallel
    await this.initProto();

    const device = await (navigator as Navigator).bluetooth.requestDevice({
      acceptAllDevices: true,
      optionalServices: [SBERCAST_BLE_SERVICE_UUID],
    });

    const gattServer = await device.gatt?.connect();

    if (!gattServer) {
      throw new Error("GATT Server is not OK!");
    }

    this.sbercastBleService = await gattServer.getPrimaryService(
      SBERCAST_BLE_SERVICE_UUID
    );

    console.log("set ble service success");

    await Promise.all([this.setReadable(), this.setWritable()]);
  }

  makeBleMessage(data): Uint8Array {
    const payload = {
      type: 1,
      data,
    };

    if (this.starBleMessageProtoType.verify(payload)) {
      throw new Error("Verify BLE message failed!");
    }

    return this.starBleMessageProtoType.encode(payload).finish();
  }

  async parseIrdResponse(data) {
    const starBleMessage = this.starBleMessageProtoType.decode(
      new Uint8Array(data.buffer).slice(5)
    );

    const response = this.irdResponseProtoType.decode(starBleMessage.data);
    console.log(response);

    return response;
  }

  async sendIrdRequest(irdRequestPayload) {
    if (this.irdRequestProtoType.verify(irdRequestPayload)) {
      throw new Error("Verify Ird request failed!");
    }

    const buffer = this.irdRequestProtoType.encode(irdRequestPayload).finish();
    console.log(this.irdRequestProtoType.decode(buffer));

    await this.setSession();

    await sendPackages(
      this.writableCharacteristic,
      toPackages(this.makeBleMessage(buffer), this.sessionId)
    );

    const responseBuffer = await this.readableCharacteristic?.readValue();
    console.log(responseBuffer);

    return this.parseIrdResponse(responseBuffer);
  }

  async getWifiList() {
    const payload = {
      requestId: Math.random().toString(),
      getWifiList: {},
      setupMode: {
        standard: {},
      },
    };

    return this.sendIrdRequest(payload);
  }

  async wifiConnectionRequest(
    wifiConnection: WifiConnectionType,
    password: string
  ) {
    const payload = {
      requestId: Math.random().toString(),
      wifiConnectRequest: {
        ssid: wifiConnection.ssid,
        wifiType: wifiConnection.wifiType,
        isHidden: false,
        password: {
          rawValue: password,
        },
      },
      setupMode: {
        standard: {},
      },
    };

    return this.sendIrdRequest(payload);
  }

  async getSetupInfo() {
    const payload = {
      requestId: Math.random().toString(),
      getSetupInfo: {},
      setupMode: {
        standard: {},
      },
    };

    return this.sendIrdRequest(payload);
  }
}
