import { useState } from "react";
import "./App.css";
import { SbercastBLEService } from "./sbercastBle.service";
import { WifiConnectionType } from "./types";

type WifiListProps = {
  wifiList: WifiConnectionType[];
};

function WifiList({ wifiList }: WifiListProps) {
  const [activeWifi, setActiveWifi] = useState<
    WifiConnectionType | undefined
  >();

  const [password, setPassword] = useState<string>("");
  const [error, setError] = useState();

  const onClick = () => {
    if (!activeWifi) {
      return;
    }

    SbercastBLEService.getInstance()
      .wifiConnectionRequest(activeWifi, password)
      .then((res) => {
        console.log(res);
        if (res?.networkConnectResponse?.error) {
          setError(res.networkConnectResponse.error.message);
        }
      });
  };

  const getSetupInfo = () => {
    SbercastBLEService.getInstance().getSetupInfo();
  };

  return (
    <div>
      {error && <h2>Error: {error}</h2>}
      <h2>Select wifi:</h2>
      <ul>
        {wifiList.map((wifi) => (
          <li
            className={activeWifi?.ssid === wifi.ssid ? "active" : undefined}
            onClick={() => setActiveWifi(wifi)}
          >
            {wifi.ssid}
          </li>
        ))}
      </ul>

      <input
        type="text"
        placeholder="wifi password"
        onChange={(e) => setPassword(e.target.value)}
      />
      <br />
      <br />
      <button
        disabled={activeWifi === undefined || password === ""}
        onClick={onClick}
      >
        send to device
      </button>
      <br />
      <button onClick={getSetupInfo}>getSetupInfo</button>
    </div>
  );
}

function App() {
  const [wifiList, setWifiList] = useState();
  const [loading, setLoading] = useState(false);

  const onConnect = () => {
    const sbercastBleService = SbercastBLEService.getInstance();
    setLoading(true);

    sbercastBleService
      .init()
      .then(() => {
        return sbercastBleService.getWifiList().then((irdResponse) => {
          setLoading(false);
          setWifiList(irdResponse.getWifiListResponse.wifiInfos);
        });
      })
      .catch((error) => {
        console.error(error);

        setLoading(false);
      });
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1
          style={{
            visibility: loading ? "visible" : "hidden",
          }}
        >
          LOADING...
        </h1>
        {wifiList === undefined && <button onClick={onConnect}>connect</button>}
        {Array.isArray(wifiList) && <WifiList wifiList={wifiList} />}
      </header>
    </div>
  );
}

export default App;
